body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.compare h1 {
    font-family: 'Libre Bodoni', serif;
    font-weight: bold;
    font-size: 2em;
    padding-top: 1px;
    color: #e5fff4;
}

.form {
    display: flex;
    justify-content: center;
}
.form_city {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 1em;
}

input[type="text"] {
    margin: 0.3em;
    box-shadow: 0px 1px 8px 0px rgb(0 0 0 / 78%);
}

.formCity_style { 
    background: rgba(30, 127, 1, 0);   
    font-weight: bold;
    font-size: 1.1em;
}

.form_city p {
    border-bottom: groove;
    font-weight: bold;
    font-size: 20px;
    margin: 2px;
}

table.table {
    color: white;
    display: flex;
    justify-content: center;
}

table.table tbody {
    border: 1px solid white;
    font-weight: bold;
    background: radial-gradient(circle, rgba(57,85,105,1) 0%, rgba(58,58,89,1) 44%, rgba(46,46,46,0.989233193277311) 100%);
    background-size: 400% 400%;


    -webkit-animation: Grays 30s ease infinite;
    animation: Grays 10s ease infinite;
}

@-webkit-keyframes Grays {
    0%{background-position:0% 71%}
    50%{background-position:100% 30%}
    100%{background-position:0% 71%}
}
@keyframes Grays {
    0%{background-position:0% 71%}
    50%{background-position:100% 30%}
    100%{background-position:0% 71%}
}


table.table tbody tr {
    padding: 1px 10px;
    border-bottom: 1px solid;
}


.active-link{
    background-image: linear-gradient(to right, #77A1D3 0%, #79CBCA  51%, #77A1D3  100%)
}
  
nav a {
    width: 130px;
    height: 40px;
    color: #fff;
    border-radius: 5px;
    padding: 10px 25px;
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
     box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
     7px 7px 20px 0px rgba(0,0,0,.1),
     4px 4px 5px 0px rgba(0,0,0,.1);
    outline: none;
    text-decoration: none;
    background-image: linear-gradient(to right, #4b6cb7 0%, #182848  51%, #4b6cb7  100%)
  }

  
   nav a:hover {
     background-position: right center;
     color: #fff;
     text-decoration: none;
   }
  

   
   
   .btn-grad {background-image: linear-gradient(to right, #77A1D3 0%, #79CBCA  51%, #77A1D3  100%)}
   .btn-grad {
      margin: 10px;
      padding: 15px 45px;
      text-align: center;
      text-transform: uppercase;
      transition: 0.5s;
      background-size: 200% auto;
      color: white;            
      box-shadow: 0 0 20px #eee;
      border-radius: 10px;
      display: block;
    }

    .btn-grad:hover {
      background-position: right center; /* change the direction of the change here */
      color: #fff;
      text-decoration: none;
    }
   
   
.row {
    text-align: center;
}

.infoWeath p{
    margin: 0;
}

.bott{
    color: #FFFFFF;
    font: bold 16px arial;
    height: 32px;
    line-height: 26px;
    text-align: center;
    background: linear-gradient(to bottom, #209DCA, #0F84AF) repeat scroll 0 0 / 100% 200% rgba(0, 0, 0, 0);
    transition: all 0.5s linear 0s;
}

.bott:hover, .bott:focus{
    background-position: 0 -102%;
}
.blockquote-5 {
    font-family: 'Sansita Swashed', cursive;
    padding: 16px 24px 5px;
    margin: 16px 16px 26px 16px;
    font-size: 16px;
    background: rgb(62 77 82 / 20%);
    border-top: 2px solid #648099;
    border-bottom: 2px solid #648099;
    display: flex;
    position: absolute;
    bottom: 0;
}
.blockquote-5 h3 {
    color: #b3d0e9;
    font-size: 15px;
    padding-top: 20px;
    display: block;
    font-weight: bolder;
}
.blockquote-5 h2 {
    color: #e5fff4;
}
.itigenik{
    background: url(/ITgenio/static/media/itgenik-02.a000042d.png);
    background-size: contain;
    background-repeat: no-repeat;
    width: 7em;
    height: 7em;
}

@media (max-width: 770px) { 
    .blockquote-5 {
        position: relative; 
        margin-top: 2em;
    }
}


.error{
    -webkit-animation: blink1 3s linear infinite;
    animation: blink1 3s cubic-bezier(1, 0.26, 1, 1) infinite;
    font-weight: bolder;
}

  @-webkit-keyframes blink1 {
    0% { color: rgb(194, 155, 155); }
    50% { color: rgba(216, 131, 131, 0.979); }
    100% { color: rgb(218, 22, 22); }
  }
  @keyframes blink1 {
    0% { color: rgb(194, 155, 155); }
    50% { color: rgba(216, 131, 131, 0.979); }
    100% { color: rgb(218, 22, 22); }
  }
